header {
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 10;
    
}

.header{
  width: 100%;
  background-color: rgba(var(--bg-c), 1);

  &__logo {
    color: rgba(var(--cl-a), 1);
    font-family: var(--font-family2);
    font-size: 40px;
    font-weight: 400;
    font-style: normal;
    letter-spacing: normal;
    line-height: normal;
    text-transform: uppercase;

    @include for-small-tablet {
      display: none;
    }
  }
}

.header-box {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  grid-gap: 40px;

  @include small-tablet{
    justify-content: space-between;
    padding: 30px 0;
  }

  &__inner{
    display: flex;
    @include small-tablet{
      display: none;
    }
  }
}
