.gallery-section{
    padding:clamp(50px, 15.584vw, 180px) 0 clamp(136px, 18.615vw, 215px);
    position: relative;


    &::before{
        position: absolute;
        content: "";
        width: 100vw;
        height: 65px;
        background-image: url(/img/index/back1.png);
        background-repeat: no-repeat;
        background-size: cover;
        bottom: 0;
        left: 50%;
        transform: translateX(-50%);

        @include mobile{
            background-size: 500%;
        }
    }
}