  --body-font-size: 16px;
  --content-width: 1155px;
  --content-width-mode: 1337px;
  --container-offset: 15px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));
  --container-width-mode: calc(var(--content-width-mode) + (var(--container-offset) * 2));
  --header-height: 49px;
  --scrollbar-thumb: #000000;
  --scrollbar-thumb-hover: #000000;
  --scrollbar-track: #474747;
  --scrollbar-track-hover: #474747;
  --overlay-bg: rgba(0, 0, 0, 0.5);
  --font-family: "Roboto" , sans-serif;
  --font-family2: "Bebas-Neue" , sans-serif;
  --font-family3: "RobotoCondensed" , sans-serif;
  --burger-cl: 0, 214, 85;
  --cl-a: 255, 255, 255;
  --cl-b: 0, 0, 0;
  --cl-c: 71, 71, 71;
  --cl-d: 107, 107, 107;
  --cl-e: 167, 167, 167;
  --bg-a: 255, 255, 255;
  --bg-b: 0, 0, 0;
  --bg-c: 10, 10, 10;
  --bg-d: 3, 3, 3;
  --bg-e: 71, 71, 71;
  --bg-f: 20, 20, 20;
  --bg-g: 82, 82, 82;
  --bg-h: 19, 19, 19;
  --bg-i: 99, 99, 99;
  --bg-j: 167, 167, 167;
  --br-a: #474747;
}
@media (max-width: 1024px) {
  :root {
    --header-height: 49px;
  }
}
@media (max-width: 767px) {
  :root {
    --header-height:108px;
  }
}
@media (max-width: 576px) {
