html {
  box-sizing: border-box;
  width: 100%;
  overflow-x: hidden;
}

body {
  position: relative;
  overflow-x: inherit;
  width: 100%;
  margin: 0;
  font-weight: 400;
  font-size: 16px;
  line-height: normal;
  font-family: var(--font-family);
  color: rgba(var(--color-b), 1);
  background: var(--body-bg);
  -webkit-text-size-adjust: 100%;
}
body.fixed-body {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  overflow: hidden;
}

main {
  padding-top: var(--header-height);
  transition: padding-top 0.5s linear;
  min-width: 0 !important;
}

*,
*::after,
*::before {
  box-sizing: border-box;
}

* {
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

a {
  display: inline-flex;
  margin: 0;
  font-style: inherit;
  font-weight: inherit;
  font-family: inherit;
  color: inherit;
  text-decoration: none;
  cursor: pointer;
}

button {
